.matrix{font-family:"Roboto";font-style:normal;padding:20px}.matrix_header{display:flex;justify-content:end}.matrix_header_column{border:solid #29394d;border-radius:5px 5px 0px 0px;border-width:1px 1px 0px 1px;height:10px;display:flex;justify-content:center;align-items:center;margin:0 0 0 8px}.matrix_header_column>div{margin-top:-19px;padding:3px 4px;color:#29394d;margin:-19px 5px 0px;text-align:center;font-weight:600;font-size:14px;line-height:16px}.matrix_header .disabled{border-color:#d8dde2}.matrix_header .disabled>div{color:#6d7f92}.matrix_body_line{display:flex;justify-content:end;border-bottom:1px solid #d8dde2;color:#29394d;font-size:14px;line-height:16px}.matrix_body_line>div{padding:5px;margin:0 0 0 8px;border:solid #29394d;border-width:0px 1px 0px 1px}.matrix_body_line>div:first-child{border:none;border-width:0px 0px 0px 0px;text-align:start}.matrix_body_line .disabled{border-color:#d8dde2}.matrix_body_line:first-child{border-bottom:1px solid #29394d}.matrix_body_child{text-align:center}.matrix_footer{display:flex;justify-content:end}.matrix_footer_column{border:solid #29394d;border-radius:0px 0px 5px 5px;border-width:0px 1px 1px 1px;height:10px;display:flex;justify-content:center;align-items:center;margin:0 0 0 8px}.matrix_footer .disabled{border-color:#d8dde2}.pointer{cursor:pointer}
