.actionButton a{display:flex;align-items:center;justify-content:center;padding:0.75rem 1.5rem;border-radius:5px;box-shadow:0 1px 10px 0 #29394d;color:#29394d;font-size:medium;font-style:normal;font-weight:500;line-height:normal;text-transform:uppercase;height:43px;transition:background-color 0.3s ease}.actionButton a:hover{background:#e1e4e8}.actionButton a.default{color:#29394d;background:#fff;border:1px solid #d8dde2}.actionButton a.default:hover{background:#e1e4e8}.actionButton a.greenTeal{color:#fff;background:#06d9b1}.actionButton a.greenTeal:hover{background:#02af8e}.actionButton a.redOrange{color:#fff;background:#fb1e5b}.actionButton a.redOrange:hover{background:#e70846}.actionButton a.blue{color:#fff;background:#18a0fb}.actionButton a.blue:hover{background:#3b8ad5}.actionButton a.orangeRoyal{color:#fff;background:#fe3745}.actionButton a.orangeRoyal:hover{background:#ec1a28}.actionButton a.lightBlue{color:#fff;background:#0080c9}.actionButton a.lightBlue:hover{background:#0f6ac8}@media print, screen and (max-width: 63.99875em){.actionButton{width:100%}}
