.wrapper{display:flex;flex-direction:column;justify-content:space-between;padding:30px;background:linear-gradient(to right, #29394d, rgba(41,57,77,0.698039), rgba(41,57,77,0));border-radius:14px;position:relative;overflow:hidden;width:100%;height:344px}.wrapper .top{display:flex;flex-direction:column;gap:10px}.wrapper .header{display:flex;justify-content:space-between;width:100%;gap:24px}.wrapper .header .titles{display:flex;flex-direction:column;gap:10px;max-width:calc(100% - 100px)}.wrapper .header .titles .label{display:flex;align-items:center;gap:10px}.wrapper .header .titles .label .text{color:#18a0fb;font-size:20px;font-style:normal;font-weight:700;line-height:normal;text-transform:uppercase}.wrapper .header .titles .label .text.blue{color:#18a0fb}.wrapper .header .titles .label .text.purple{color:#6f6cec}.wrapper .header .titles .label .text.orange{color:#f47f4d}.wrapper .header .titles .label .text.greenTeal{color:#14b7ad}.wrapper .header .titles .label .text.redOrange{color:#fb1e5b}.wrapper .header .titles .label .text.lightBlue{color:#bce4fa}.wrapper .header .titles .title{color:#fff;font-size:24px;font-style:normal;font-weight:600;line-height:120%;cursor:pointer;transition:text-decoration 0.3s ease}.wrapper .header .titles .title:hover{text-decoration:underline}.wrapper .header .clientImg{display:flex;align-items:center;justify-content:center;border-radius:5px;background:#fff;padding:4px;height:fit-content;width:fit-content}.wrapper .header .clientImg img{width:68px;height:38px;object-fit:contain}.wrapper .body{display:flex;flex-direction:column;max-width:calc(100% - 100px)}.wrapper .footer{display:flex;flex-direction:row}.fetching{background-color:#fff}@media print, screen and (max-width: 63.99875em){.wrapper{padding:20px;height:404px !important;width:100% !important;max-width:753px}.wrapper .header .titles .label{flex-wrap:wrap}.wrapper .header .titles .label .text{font-size:14px}.wrapper .header .titles .title{font-size:18px;width:calc(100% + 100px)}.wrapper .body{max-width:100%}.wrapper .footer{flex-direction:column}}
