.ttp-switch{position:absolute;opacity:0}.ttp-switch+div{vertical-align:middle;transition-duration:0.4s;transition-property:background-color, box-shadow;cursor:pointer;width:40px;height:16px;background-color:#dbe0e5;border-radius:20px}.ttp-switch+div>div{position:relative;top:-25%;left:0;width:24px;height:24px;background:#b6bfc8;border:1px solid #ffffff;border-radius:50%;box-shadow:0px 4px 10px rgba(41,57,77,0.1);transition-timing-function:cubic-bezier(1, 0, 0, 1);transition-duration:0.4s;transition-property:transform, background-color;pointer-events:none}.ttp-switch+div span{position:absolute;font-size:1.6rem;color:white;margin-top:12px}.ttp-switch+div span:nth-child(1){margin-left:15px}.ttp-switch+div span :nth-child(2){margin-left:55px}.ttp-switch:checked+div{background-color:#c7e8fe}.ttp-switch:checked+div>div{background-color:#18a0fb;transform:translate3d(16px, 0, 0)}
