.articleSlide{padding-bottom:10px}.articleSlide .category{display:flex;justify-content:center;align-items:center;padding:2px 6px;min-width:45px;min-height:15px;border-radius:5px;background:#f8626d;color:#fff;font-size:12px;font-style:normal;font-weight:400;line-height:normal;width:fit-content}.articleSlide .footer{flex-direction:column}.articleSlide .footer .infos{display:flex;align-items:center;gap:9px}.articleSlide .footer .infos .publishedAt,.articleSlide .footer .infos .readTime{color:#f1f2f4;font-size:12px;font-style:normal;font-weight:400;line-height:normal}.articleSlide .footer .infos .dot{width:5px;height:5px;background-color:#f1f2f4;border-radius:50%}.articleSlide .footer hr{border:0.25px solid #e1e4e8;width:100%}.articleSlide .footer .actions{display:flex;align-items:center;justify-content:space-between}.articleSlide .footer .actions .reactActions{display:flex;align-items:center;gap:26px}.articleSlide .footer .actions .reactActions .likes,.articleSlide .footer .actions .reactActions .comments{display:flex;align-items:center;gap:10px;color:#f1f2f4;font-size:12px;font-style:normal;font-weight:700;line-height:normal}.articleSlide .footer .actions .reactActions .likes path,.articleSlide .footer .actions .reactActions .comments path{fill:#b2bcc6}.articleSlide .footer .actions .reactActions .likes.active path,.articleSlide .footer .actions .reactActions .comments.active path{fill:#f8626d}.articleSlide .footer .actions .reactActions.allowedInteraction .likes,.articleSlide .footer .actions .reactActions.allowedInteraction .comments{cursor:pointer}.articleSlide .footer .actions .reactActions.allowedInteraction .likes:hover path,.articleSlide .footer .actions .reactActions.allowedInteraction .comments:hover path{fill:#6d7f92}.articleSlide .footer .actions .share{position:relative;color:#f1f2f4;text-align:right;font-size:12px;font-style:normal;font-weight:700;line-height:normal;cursor:pointer}.articleSlide .footer .actions .sharePopup{position:absolute;top:-50px;left:-50%;display:flex;padding:5px 10px;transform:translateX(-50%);background:#f1f2f4;border-radius:5px;border:1px solid #fff}.articleSlide .footer .actions .sharePopup .action{position:relative;cursor:pointer;width:32px !important;height:32px;border-radius:50px;background-color:#fff;margin-right:14px;font-size:10px;line-height:11px;display:flex;align-items:center;justify-content:center;color:#f1f2f4}
