.premiumSlide .body{display:flex;flex-direction:column;gap:10px}.premiumSlide .body .note{color:#d8dde2;text-shadow:0px 2px 10px rgba(41,57,77,0.1);font-size:12px;font-style:italic;font-weight:400;line-height:normal}.premiumSlide .body .plans{display:flex;gap:10px}.premiumSlide .body .plans .plan{display:flex;flex-direction:column;min-width:220px;max-width:300px;min-height:70px;padding:10px;border-radius:5px;border:1px solid #6d7f92}.premiumSlide .body .plans .plan .label{color:#fb1e5b;font-size:12px;font-style:normal;font-weight:500;line-height:140%}.premiumSlide .body .plans .plan .price .amount{color:#fff;font-size:24px;font-style:normal;font-weight:700;line-height:normal;text-transform:lowercase}.premiumSlide .body .plans .plan .price .currency{color:#fff;font-size:16px;font-style:normal;font-weight:700;line-height:normal;text-transform:lowercase}.premiumSlide .body .plans .plan .price .period{margin-left:10px;color:#fff;font-size:12px;font-style:normal;font-weight:400;line-height:140%;text-transform:lowercase}.premiumSlide .body .plans .plan .description{margin-top:3px;color:#b2bcc6;font-size:12px;font-style:normal;font-weight:400;line-height:140%}.premiumSlide .body .plans .plan .infos{margin-top:3px}.premiumSlide .body .plans .plan .infos .originalPrice{position:relative;color:#de4848;font-size:16px;font-style:normal;font-weight:600;line-height:140%}.premiumSlide .body .plans .plan .infos .originalPrice::after{position:absolute;content:"";width:100%;height:1px;border-radius:1px;background:#de4848;top:50%;right:0}.premiumSlide .body .plans .plan .infos .discount{margin-left:10px;color:#06d9b1;font-size:12px;font-style:normal;font-weight:500;line-height:140%;text-transform:lowercase}.premiumSlide .subscribed{color:#39c7a5;font-size:1rem;font-style:normal;font-weight:500;line-height:normal}@media print, screen and (max-width: 63.99875em){.premiumSlide .plans{flex-direction:column}}
