.time_counter{display:flex;flex-direction:row;align-items:center;gap:5px}.time_counter span{color:#fff;font-size:20px;font-style:normal;font-weight:700;line-height:normal;margin-bottom:4px}.time_counter .counter_case{display:flex;justify-content:center;align-items:center;flex-direction:row;gap:3px;width:48px;height:33px;font-size:16px;font-style:normal;font-weight:500;line-height:normal;border:1px solid #fff;border-radius:5px;color:#fff;background:#29394db3}.time_counter .counter_case span{font-size:12px;font-weight:400;padding-top:2.5px;margin-bottom:0}
