.price_container{display:flex;flex-direction:row;gap:10px;align-items:center}.price_container .price{color:#fff;font-size:24px;font-style:normal;font-weight:700;line-height:normal}.price_container .price span{font-size:16px}.price_container .original_price{position:relative;color:#eb5757;font-size:20px;font-style:normal;font-weight:500;line-height:normal;text-transform:lowercase}.price_container .original_price::after{position:absolute;content:"";width:100%;height:1px;border-radius:2px;background:#de4848;top:50%;right:0}.price_container .member_discount{width:85px;min-height:42px;display:flex;flex-direction:column;justify-content:center;align-items:flex-end}.price_container .member_discount .discount_percent{color:#d8dde2;font-size:16px;font-style:normal;font-weight:500;line-height:normal;text-align:right}.price_container .member_discount .discount_description{color:#d8dde2;font-size:10px;font-style:normal;font-weight:500;line-height:normal;text-align:right}@media print, screen and (max-width: 63.99875em){.price_container{width:100%;justify-content:space-between}.price_container .price{font-size:1.125rem}}
